import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { Watchlist } from "@rp/common/api-types"
import { formatTime } from "@rp/common/utils"
import { ActionsMenu } from "@rp/react/web/common/base-components"
import { HubTableRow } from "@rp/react/web/common/composite-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box } from "@rp/react/web/mui/core"
import { WATCHLIST_EDIT, WATCHLIST_ID_ROUTE } from "@rp/react/web/router"
import { useWatchlistsActions } from "../hooks/useWatchlistsActions"

type Props = {
  item: Watchlist
  columnsWidth: Array<string>
}

export const MyWatchlistsListItem = ({ item, columnsWidth }: Props) => {
  const { share, copy, search, rename, remove, favorite } = useWatchlistsActions()
  const actions = [share, favorite, copy, search, rename, remove]
  const { isMobile } = useDeviceInfo()
  const navigate = useNavigate()
  const [openMenu, setOpenMenu] = useState(false)

  return (
    <HubTableRow
      aria-label={item.name}
      sx={{
        "&:hover": { backgroundColor: "grey.800" },
        gap: 0,
        "& > div": { pr: 2 },
        "& > div:last-of-type": { pr: 0 },
      }}
      selected={openMenu}
      onRowClick={() => {
        if (isMobile) {
          navigate(WATCHLIST_EDIT.replace(":id", item.id))
        } else {
          navigate(WATCHLIST_ID_ROUTE.replace(":id", item.id), { state: { comingFrom: "user" } })
        }
      }}
      showCompanyIcon={item.shared?.company.permission !== "undefined"}
      columnsWidth={columnsWidth}
      columns={[
        { text: item.name },
        { text: formatTime(item.lastInteracted ?? item.lastUpdated) },
        {
          node: (
            <Box sx={{ display: "flex", justifyContent: "right", alignItems: "center" }}>
              <ActionsMenu open={openMenu} setOpen={setOpenMenu} actions={actions} item={item} />
            </Box>
          ),
        },
      ]}
    />
  )
}
